.link {
  position: relative;
  height: 50px;
  width: 200px;
  border-radius: 50px;
  background-color: #f53f3f;
  color: white;
  font-size: 1.4rem;
  padding: 0 2rem;
  background:
    linear-gradient(-45deg, #ea4325, #e7ea25, #ea4325);
  background-size: 400%;
  background-position: 90% 0;
  color: #ffffff;
  transition: background 0.8s;

}
.link:hover {
  background-position: 185% 50%;
  letter-spacing: 2px;
}

.link::before {
  top: 0;
}

.link::after {
  bottom: 0;
}

.link::before,
.link::after {
  transform: scalex(1);
}

.link::before,
.link::after {
  content: "";
  position: absolute;
  left: 0;
  transform: scaleX(0);
  width: 100%;
  height: 0.5px;
  background: white;
  transition: all 0.3s;
}

.link .Git-link {
  color: aliceblue;
  font-size: 1.4rem;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-style: inherit;
}