:root{
    --bg-button: #f0f0f0;
    --border-button:solid 1px #888;

}
.Button{
    font-size: 1.8rem;
    background-color: var(--bg-button);
    border: none;
    border-right: var(--border-button);
    border-bottom: var(--border-button);
    outline: none;
    cursor: pointer;
    
}
.Button:active{
    background-color: #ccc;
}
.Button.double{
    grid-column: span 2;
}
.Button.triple{
    grid-column: span 3;
}
.Button.operation{
    background-color: #f0483c;
    color: white;
    font-size: 1.5rem;
    padding: 0 2rem;
    background: 
    linear-gradient(
        -45deg, #4819f1,#7e11b1,#4819f1
    );
    background-size: 400%;
    background-position: 90% 0;
    color: #ffffff;
    transition: background 0.8s;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-style: inherit;
}
.Button.operation:hover{
    background-position: 185% 50%;
}
.Button.operation:active{
    background: 
    linear-gradient(
        -45deg, #4819f1cc,#7e11b1cc,#4819f1cc
    );
    
}
.Button.clear{
    background-color: #f53f3f;
    color: white;
    font-size: 1.4rem;
    padding: 0 2rem;
    background: 
    linear-gradient(
        -45deg, #ea4325,#e7ea25,#ea4325
    );
    background-size: 400%;
    background-position: 90% 0;
    color: #ffffff;
    transition: background 0.8s;
}
.Button.clear:hover{
    background-position: 185% 50%;
}

.Button.clear:active{
    grid-column: span 3;
    color: white;
    font-size: 1.4rem;
        background: 
        linear-gradient(
            -45deg, #ea4325cc,#e7ea25cc,#ea4325cc
        );
}